let saidHello = false;
let renderType = 'canvas';
const VERSION = '__VERSION__';

/**
 * 跳过版本说明
 *
 * @function skipHello
 * @memberof PIXI.utils
 */
export function skipHello(): void
{
    saidHello = true;
}

/**
 * 输出引擎版本信息
 *
 * @static
 * @function versionPrint
 * @memberof PIXI.utils
 * @param {string} version - 版本号 (gui 1.1.1).
 * @param {string} [docsSite=‘’] - 可访问文档地址.
 */
export function versionPrint(version: string, docsSite = 'https://github.com/vipkid-edu/vf-docs'): void
{
    if (navigator.userAgent.toLowerCase().indexOf('chrome') > -1)
    {
        const args = [
            `\n %c %c %c VFJS ${version} - ✰ ${renderType} ✰  %c  %c  ${docsSite}  %c  %c \n\n`,
            'background: #ff9932; padding:5px 0;',
            'background: #ff9932; padding:5px 0;',
            'color: #ff66a5; background: #030307; padding:5px 0;',
            'background: #ff9932; padding:5px 0;',
            'background: #ff9932; padding:5px 0;',
            'background: #ff9932; padding:5px 0;',
            'color: #ff2424; background: #fff; padding:5px 0;',
        ];

        window.console.log(...args);
    }
    else if (window.console)
    {
        window.console.log(`VFJS ${version} - ${renderType} - ${docsSite}`);
    }
}

/**
 * 欢迎日志输出，输出版本信息与配置信息
 *
 * @static
 * @function sayHello
 * @memberof PIXI.utils
 * @param {string} type - The string renderer type to log.
 */
export function sayHello(type: string): void
{
    renderType = type;

    if (saidHello)
    {
        return;
    }

    versionPrint(`engine ${VERSION}`);

    saidHello = true;
}

