/* eslint-disable brace-style */
import { isMobile } from './isMobile';
import { ISystemInfo } from 'vfInterface';

let systemInfo: ISystemInfo = null;

function checkDevice(obj: ISystemInfo): void {
    const ua = navigator.userAgent;

    obj.os.name = navigator.platform;
    if (ua.match('Unix')) obj.os.name = 'Unix';
    if (ua.match('Linux')) obj.os.name = 'Linux';
    let match: RegExpExecArray;

    if (isMobile.tablet || isMobile.phone) {
        if (isMobile.apple.device)
        {
            obj.os.name = 'iOS';
            match = (/OS (.*) like Mac OS X/).exec(ua);
            // match = ua.match(/OS (\d+)_(\d+)?/);
            if (match) {
                obj.os.version = match[1].replace(/_/g, '.');
            }
            if (ua.match('iPhone Simulator;')) {
                obj.device.name = 'iPhone';
                obj.device.type = 'emulator';
            }
            else if (ua.match('iPod;')) {
                obj.device.name = 'iPod Touch';
                obj.device.type = 'media';
            }
            else if (ua.match('iPhone;') || ua.match(/iPhone\s*\d*s?[cp]?;/i)) {
                obj.device.name = 'iPhone';
                obj.device.type = 'mobile';
            }
            else {
                obj.device.name = 'iPad';
                obj.device.type = 'tablet';
            }
        }
        if (isMobile.android.device) {
            obj.os.name = 'Android';
            match = (/Android(?: )?(?:AllPhone_|CyanogenMod_)?(?:\/)?v?([0-9.]+)/).exec(ua.replace('-update', '.'));
            if (match) {
                obj.os.version = match[1];
            }
            obj.device.name = 'Android Device';
            obj.device.type = 'mobile';
            if (isMobile.tablet) obj.device.type = 'tablet';
            if (isMobile.phone) obj.device.type = 'mobile';
        }
    }
    else if (ua.match('Mac OS X')) {
        obj.os.name = 'Mac OS X';
        match = (/Mac OS X (10[0-9\._]*)/).exec(ua);
        if (match) {
            obj.os.version = match[1].replace(/_/g, '.');
        }
        obj.device.name = 'Apple Computer';
        obj.device.type = 'pc';
    }
    else if (ua.match('Windows')) {
        obj.os.name = 'Windows';
        match = (/Windows NT ([0-9]\.[0-9])/).exec(ua);
        if (match) {
            obj.os.version = match[1];
            switch (match[1]) {
                case '6.2':
                    obj.os.version = '8';
                    break;
                case '6.1':
                    obj.os.version = '7';
                    break;
                case '6.0':
                    obj.os.version = 'Vista';
                    break;
                case '5.2':
                    obj.os.version = 'Server 2003';
                    break;
                case '5.1':
                    obj.os.version = 'XP';
                    break;
                case '5.0':
                    obj.os.version = '2000';
                    break;
                default:
                    obj.os.version = `NT ${obj.os.version}`;
            }
        }
        obj.device.name = 'Windows Computer';
        obj.device.type = 'pc';
        if (ua.match('WindowsMobile') || ua.match('Windows Phone')) {
            obj.device.name = 'Windows Phone';
            obj.device.type = 'mobile';
            obj.os.name = 'Windows Mobile';
            obj.os.version = 'x';
        }
    }
}

function checkBrowser(obj: ISystemInfo): void
{
    const ua = navigator.userAgent;
    let match = (/WebKit\/([0-9.]*)/i).exec(ua);

    if (match) {
        obj.browser.name = 'Webkit';
        obj.browser.version = match[1];
    }
    match = (/Browser\/AppleWebKit([0-9.]*)/i).exec(ua);
    if (match) {
        obj.browser.name = 'Webkit';
        obj.browser.version = match[1];
    }
    if (ua.match('Safari')) {
        obj.browser.name = 'Safari';
        match = (/Version\/([0-9\.]+)/).exec(ua);
        if (match)
        {
            obj.browser.version = match[1];
        }
    }
    if (ua.match('MSIE')) {
        obj.browser.name = 'Internet Explorer';
        match = (/MSIE ([0-9.]*)/).exec(ua);
        if (match) {
            obj.browser.version = match[1];
        }
    }
    if (ua.match('Edge')) {
        obj.browser.name = 'Edge';
        obj.browser.version = 'x';
    }
    if (ua.match(/Opera/i)) {
        obj.browser.name = 'Opera';
        match = (/Opera[\/| ]([0-9.]*)/).exec(ua);
        if (match) {
            obj.browser.version = match[1];
        }
    }
    if (ua.match('Firefox')) {
        obj.browser.name = 'Firefox';
        match = (/Firefox\/([0-9ab.]*)/).exec(ua);
        if (match) {
            obj.browser.version = match[1];
        }
    }
    match = (/(?:Chrome|CrMo|CriOS)\/([0-9.]*)/).exec(ua);
    if (match) {
        obj.browser.name = 'Chrome';
        obj.browser.version = match[1];
    }
    if (ua.match('Chromium')) {
        obj.browser.name = 'Chromium';
        match = (/Chromium\/([0-9.]*)/).exec(ua);
        if (match) {
            obj.browser.version = match[1];
        }
    }
    if (ua.match('UCWEB')) {
        obj.browser.name = 'UC';
        match = (/UCWEB([0-9]*[.][0-9]*)/).exec(ua);
        if (match) {
            obj.browser.version = match[1];
        }
    }
    match = (/UCBrowser\/([0-9.]*)/).exec(ua);
    if (match) {
        obj.browser.name = 'UC';
        obj.browser.version = match[1];
    }
    match = (/(M?QQBrowser)\/([0-9.]*)/).exec(ua);
    if (match) {
        obj.browser.name = 'QQ';
        let version = match[2];

        if (version.match(/^[0-9][0-9]$/)) version = `${version[0]}.${version[1]}`;
        obj.browser.version = version;
        /* if (!obj.os.name && match[1] === 'QQBrowser') {
            obj.os.name = 'Windows';
        }*/
    }
    // ..ignore others..
}

/**
 * 获取设备信息
 * @private
 */
export function getSystemInfo(): ISystemInfo {
    if (systemInfo === null) {
        systemInfo = {} as any;
        const sysInfo = systemInfo;

        sysInfo.browser = {} as any;
        sysInfo.device = {} as any;
        sysInfo.os = {} as any;

        if (screen) {
            sysInfo.resolution = `${screen.width}x${screen.height}`;
        }
        if (navigator) {
            sysInfo.ua = navigator.userAgent;
            sysInfo.language = navigator.language;
        }

        sysInfo.pageUrl = document.location.href;
        sysInfo.pageReferrer = document.referrer;

        checkDevice(sysInfo);
        checkBrowser(sysInfo);
    }

    return systemInfo;
}

